	module.exports = {"data":{"talkJson":{"description":"Presenter(s): Rusty Russell\nURL: http://2011.linux.conf.au/programme/schedule/view_talk/190\n\nWhile working on CCAN I realised just how many C coding tricks there are.  Regrettably, we spend very little time reading completely foreign code and instead we collect techniques one by one over years.\r\n\r\nIt needn't be that way: this presentation is a cookbook with examples of all the C programming tricks I have found value in.  Not just the basics like valgrind, talloc and the Jenkins hash, but less sane techniques like typesafe callbacks, runtime code updating, C as a scripting language and malloc failure path testing.\r\n\r\nEveryone who codes in C will get something out of this talk.\n\nhttp://2011.linux.conf.au/ - http://www.linux.org.au\nCC BY-SA - http://creativecommons.org/licenses/by-sa/4.0/legalcode.txt","meta":{"duration":"PT46M53S"},"presenter":{"name":null},"slug":"advanced-c-coding-for-fun","source":{"id":"BEQ3sRakIs0","provider":"youtube"},"tag":["LCA2011","Linux","Open Source"],"title":"Advanced C Coding For Fun!"},"allTalkJson":{"edges":[{"node":{"description":"Richard Stallman in Zurich: The father of the movement for free software about his rebellion against administrative slack at MIT, about \"foolish\" Apple aficionados, current fights against evil software companies and Swiss national hero William Tell, who could serve as a role model in the fight against an unjust copyright.\n\nBy the way: After the camera was turned off, Richard and I talked about some other topics: Ads in the web and his Free Software song. This part is available as audio recording here, just skip to minute 20:37: http://www.stadtfilter.ch/DigitalPodcast/Digital20160517","meta":{"duration":"PT55M31S"},"presenter":{"name":"Richard Stallman"},"source":{"id":"fkkDvKGcNSo","provider":"youtube"},"slug":"apple-fanboys-are-foolish-people","tag":["Free Software foundation","Interview","MIT","Open Source"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/fkkDvKGcNSo/default.jpg","height":90,"width":120}},"title":"Apple fanboys are foolish people"}},{"node":{"description":"Linus Torvalds transformed technology twice — first with the Linux kernel, which helps power the Internet, and again with Git, the source code management system used by developers worldwide. In a rare interview with TED Curator Chris Anderson, Torvalds discusses with remarkable openness the personality traits that prompted his unique philosophy of work, engineering and life. \"I am not a visionary, I'm an engineer,\" Torvalds says. \"I'm perfectly happy with all the people who are walking around and just staring at the clouds ... but I'm looking at the ground, and I want to fix the pothole that's right in front of me before I fall in.","meta":{"duration":"PT21M31S"},"presenter":{"name":"Linus Torvalds"},"source":{"id":"o8NPllzkFhE","provider":"youtube"},"slug":"the-mind-behind-linux","tag":["TED Talk","Linux","Git","Interview","open-source"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/o8NPllzkFhE/default.jpg","height":90,"width":120}},"title":"The mind behind Linux"}}]}},"pathContext":{"slug":"advanced-c-coding-for-fun","tags":["LCA2011","Linux","Open Source"]}}

